import React, { memo } from 'react';
import cn from 'classnames';
import { useSelector } from '../../hooks';
import Preloader from '../preloader/preloader';
import styles from './order-details.module.css';
const checkIcon = require('../../images/check.svg');

const OrderDetails = () => {
  const { orderRequest, orderFailed, createOrder } = useSelector(
      (store: any) => store.ingredients
  );

  const number = createOrder?.order.number

  return (
      <div className={cn(styles.order, 'p-15')}>
        {orderRequest && <Preloader />}
        {orderFailed && ' '}
        {!orderRequest && !orderFailed && (
            <>
              <h1
                  className={cn(
                      styles.order__title,
                      'text',
                      'text_type_digits-large',
                      'mb-8'
                  )}
              >
                {number}
              </h1>
              <p
                  className={cn(styles.order__text, 'text', 'text_type_main-medium')}
              >
                {' '}
                 
              </p>
              <img
                  src={checkIcon}
                  alt=''
                  className={cn(styles.order__image, 'mt-15', 'mb-15')}
              />
              <p
                  className={cn(
                      styles.order__text,
                      'text',
                      'text_type_main-default',
                      'mb-2'
                  )}
              >
                {' '}
                   
              </p>
              <p
                  className={cn(
                      styles.order__text,
                      'text',
                      'text_type_main-default',
                      'text_color_inactive'
                  )}
              >
                {' '}
                    
              </p>
            </>
        )}
      </div>
  );
}

export default memo(OrderDetails);
